<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="`${GETTER_MECHANTNAME !== '' ? GETTER_MECHANTNAME + ' - ' : ''}打卡`" :routeTarget="{name: 'Punch'}"></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">
      <div class="mb-4 flex items-center justify-between gap-2">
        <month-picker-input
        lang="zh"
        :input-pre-filled="true"
        :default-month="+this.$dayjs().month()+1"
        :default-year="+$dayjs(new Date()).format('YYYY')"
        @change="showDate"
        class="!w-full"
      />
      </div>
      <div class="text-sm" v-if="Array.isArray(ListHasResultAttendanceRecords) && ListHasResultAttendanceRecords.length > 0">
        <!-- header -->
        <div class="flex flex-nowrap gap-1 border-b border-solid border-primary text-left">
          <div class="w-1/6">時間</div>
          <div class="w-2/6">位置</div>
          <div class="w-1/6">狀態</div>
          <div class="w-2/6">詳情</div>
          <div class="w-1/6 text-center">遲到</div>
        </div>

        <div v-for="(item,key) in ListHasResultAttendanceRecords" :key="`resultAttendance${key}`">
          <div class="mt-4 mb-1 text-left text-second">{{item.date | dateFmtMMDDdd}}</div>
          <div v-for="(clockInRecordItem,clockInRecordIndex) in item.clockInRecord" :key="`clockInRecordItem${clockInRecordIndex}`">
          <!-- {{clockInRecordItem}} -->
          <template v-if="clockInRecordItem.in">
            <div class="mb-2 flex flex-nowrap gap-1 border-b border-solid border-primary text-left">
              <div class="w-1/6">{{clockInRecordItem.in.time | dateFmtHm}}</div>
              <div class="w-2/6 overflow-hidden text-ellipsis text-skyblue">{{clockInRecordItem.in.latitude || '--'}}{{clockInRecordItem.in.longitude ? `,${clockInRecordItem.in.longitude}`: ',--'}}</div>
              <div class="w-1/6">{{item.isLate? '遲到':'成功'}}</div>
              <!-- <div class="w-2/6">{{clockInRecordIndex === 0 ? '上班打卡': '' }}</div> -->
              <div class="w-2/6">上班打卡</div>
              <div class="w-1/6 text-center" :class="{'text-rejectText': item.lateMinutes > 0}">
                <div>{{clockInRecordIndex === 0 ? item.lateMinutes: '--'}}</div>
              </div>
            </div>
          </template>
          <template v-if="clockInRecordItem.breakRecord">
          <template v-for="(breakRecord,breakRecordIndex) in clockInRecordItem.breakRecord">
            <div class="mb-2 flex flex-nowrap gap-1 border-b border-solid border-primary text-left" v-if="breakRecord.start" :key="`breakRecordSatrt${breakRecordIndex}`">
              <div class="w-1/6">{{breakRecord.start.time | dateFmtHm}}</div>
              <div class="w-2/6 overflow-hidden text-ellipsis text-skyblue">{{breakRecord.start.latitude || '--'}}{{breakRecord.start.longitude ? `,${breakRecord.start.longitude}`: ',--'}}</div>
              <div class="w-1/6">成功</div>
              <div class="w-2/6">外出打卡</div>
              <div class="w-1/6 text-center">
                <div>--</div>
              </div>
            </div>
            <div class="mb-2 flex flex-nowrap gap-1 border-b border-solid border-primary text-left" v-if="breakRecord.end" :key="`breakRecordEnd${breakRecordIndex}`">
              <div class="w-1/6">{{breakRecord.end.time | dateFmtHm}}</div>
              <div class="w-2/6 overflow-hidden text-ellipsis text-skyblue">{{breakRecord.end.latitude || '--'}}{{breakRecord.end.longitude ? `,${breakRecord.end.longitude}`: ',--'}}</div>
              <div class="w-1/6" :class="{'text-rejectText': breakRecord.end.isLate}"> {{breakRecord.end.isLate ? '失敗' : '成功' }}</div>
              <div class="w-2/6">歸來打卡</div>
              <div class="w-1/6 text-center">
                <div>--</div>
              </div>
            </div>
          </template>
          </template>
          <template v-if="clockInRecordItem.out">
            <div class="mb-2 flex flex-nowrap gap-1 border-b border-solid border-primary text-left">
              <div class="w-1/6">{{clockInRecordItem.out.time | dateFmtHm}}</div>
              <div class="w-2/6 overflow-hidden text-ellipsis text-skyblue">{{clockInRecordItem.out.latitude || '--'}}{{clockInRecordItem.out.longitude ? `,${clockInRecordItem.out.longitude}`: ',--'}}</div>
              <div class="w-1/6">{{item.isLeaveEarly ? '早退':'成功'}}</div>
              <!-- <div class="w-2/6">{{ clockInRecordIndex + 1 === item.clockInRecord.length  ? '下班打卡':''}}</div> -->
              <div class="w-2/6">下班打卡</div>
              <div class="w-1/6 text-center">
                <div>--</div>
              </div>
            </div>
          </template>
          </div>
        </div>
      </div>
      <div v-else class="mt-3 flex items-center justify-center text-2xl text-second">
        <p>查無資料</p>
      </div>
    </section>
  </main>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import { MonthPickerInput } from 'vue-month-picker';

export default {
  name: 'punch-list',
  description: '打卡列表',
  metaInfo: {
    title: '名留',
    titleTemplate: '%s | 打卡',
  },
  components: {
    MonthPickerInput,
  },
  data() {
    return {
      resultAttendance: [],
      date: {
        from: null,
        to: null,
        month: null,
        year: 2022,
      },
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    async init() {
      await Promise.all([this.getAttendanceRecords()]).then(() => {});
    },
    async getAttendanceRecords() {
      const sumbitAttendanceRecordsForm = {
        userId: this.userInfo.user.id,
        dateRange: {
          startDate: this.$dayjs(this.date.from).startOf('month').valueOf(),
          endDate: this.$dayjs(this.date.to).add(-1, 'day').endOf('month').valueOf(),
        },
        pageLimit: 1000,
      };
      const resp = await this.$api.attendanceRecords(sumbitAttendanceRecordsForm);
      const { data, errors } = resp.data;
      if (errors) return;
      console.log(data.attendanceRecords[0]);
      const [resultAttendance = {}] = data.attendanceRecords;
      this.resultAttendance = resultAttendance.attendanceRecord ? resultAttendance.attendanceRecord : [];
    },
    doRoute() {
      this.$router.push({ name: 'Punch' });
    },
    showDate(date) {
      this.date = date;
      this.getAttendanceRecords();
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo', 'currentMechantId']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
    ListHasResultAttendanceRecords() {
      const ListHasResultAttendanceRecords = this.resultAttendance.filter((records) => Array.isArray(records.clockInRecord) && records.clockInRecord.length > 0);
      return ListHasResultAttendanceRecords;
    },
  },
};
</script>

<style lang="scss" scoped>
::v-deep .month-picker-input{
  width: 100%;
}
::v-deep .month-picker__month.selected{
  background-color: #3c5091;
}

</style>
